"use server";

import { revalidatePath } from "next/cache";
import { createClient } from "@/utils/supabase/server";

// Alternative: Return success instead of redirecting, let client handle navigation
export async function signupClientSide(formData: FormData) {
  console.log(
    "=== SIGNUP ACTION START (CLIENT SIDE) ===",
    new Date().toISOString()
  );

  const supabase = await createClient();

  const password = formData.get("password") as string;
  const confirmPassword = formData.get("confirmPassword") as string;
  const email = formData.get("email") as string;

  console.log("Signup attempt for email:", email);

  // Validate password match
  if (password !== confirmPassword) {
    console.log("Password mismatch");
    return { error: "Passwords do not match" };
  }

  const data = {
    email: email,
    password: password,
  };

  console.log("Calling supabase.auth.signUp...");
  const { data: authData, error } = await supabase.auth.signUp(data);

  console.log("SignUp result:", {
    user: authData.user?.id,
    session: !!authData.session,
    error: error?.message,
  });

  if (error) {
    console.log("Signup error:", error.message);
    return { error: error.message };
  }

  if (authData.user && !authData.session) {
    return {
      success: true,
      needsConfirmation: true,
      message:
        "Please check your email and click the confirmation link to continue.",
    };
  }

  if (authData.session) {
    // Create initial onboarding record
    try {
      await supabase.from("user_onboarding").insert({
        user_id: authData.user?.id,
        payment_completed: false,
        created_at: new Date().toISOString(),
        updated_at: new Date().toISOString(),
      });
    } catch (error) {
      console.log("Note: Could not create onboarding record:", error);
    }

    revalidatePath("/", "layout");

    // Return success, let client handle navigation
    return {
      success: true,
      session: {
        access_token: authData.session.access_token,
        refresh_token: authData.session.refresh_token,
        user: authData.user,
      },
    };
  }

  return {
    error:
      "Signup completed but no session was created. Please try logging in.",
  };
}
